import { Component, OnInit, Type, ViewEncapsulation } from '@angular/core';
import { map } from 'rxjs/operators';
import { FundamentalModalManagerService } from '../../services/modal/modal-manager.service';
import { Observable } from 'rxjs';
import { IFundamentalModalButtonOptions } from '../../interfaces';

@Component({
    selector: 'fundamental-modal-template',
    encapsulation: ViewEncapsulation.None,
    providers: [FundamentalModalManagerService],
    styles: [
        `
            .fd-modal__title {
                min-height: 20px;
            }
        `
    ],
    template: `
        <fd-modal-header>
            <h1 fd-modal-title id="fd-modal-title-{{ (title | async) || '' }}">
                {{ (title | async) || '' | translate }}
                {{ (titleSuffix | async) || '' | translate }}
            </h1>
            <button
                fd-modal-close-btn
                *ngIf="isDismissButtonVisible | async"
                (click)="dismiss()"
            ></button>
        </fd-modal-header>
        <fd-modal-body>
            <ng-container *ngComponentOutlet="component | async"></ng-container>
        </fd-modal-body>
        <fd-modal-footer *ngIf="hasButtons() | async">
            <button
                *ngFor="let button of buttons | async"
                [disabled]="button.disabledFn ? button.disabledFn() : button.disabled"
                [options]="button.style"
                [attr.id]="button.id"
                (click)="onButtonClicked(button)"
                fd-button
            >
                {{ button.label | translate }}
            </button>
        </fd-modal-footer>
    `
})
export class FundamentalModalTemplateComponent implements OnInit {
    public component: Observable<Type<any>> = this.modalManager.getComponent();
    public title: Observable<string> = this.modalManager.getTitle();
    public titleSuffix: Observable<string> = this.modalManager.getTitleSuffix();
    public buttons: Observable<IFundamentalModalButtonOptions[]> = this.modalManager.getButtons();
    public isDismissButtonVisible: Observable<
        boolean
    > = this.modalManager.getIsDismissButtonVisible();

    constructor(private modalManager: FundamentalModalManagerService) {}

    ngOnInit() {
        this.modalManager.init();
    }

    public onButtonClicked(button: IFundamentalModalButtonOptions) {
        this.modalManager.onButtonClicked(button);
    }

    public dismiss(): void {
        this.modalManager.dismiss();
    }

    public hasButtons(): Observable<boolean> {
        return this.buttons.pipe(
            map((buttons: IFundamentalModalButtonOptions[]) => buttons && !!buttons.length)
        );
    }
}
