import { FormControl } from '@angular/forms';

import { AbstractForm } from './abstract-form';
import { ComponentType } from './interfaces';
import { InputProperties } from './input-properties';
import { ValidatorParameters } from './validator-parameters';

/**
 * A leaf node of forms.
 */
export class FormField extends AbstractForm {
    /**
     * @override
     */
    control!: FormControl;

    constructor(
        component: ComponentType,
        control: FormControl,
        inputs: InputProperties,
        persist: boolean,
        public validators: ValidatorParameters = new ValidatorParameters()
    ) {
        super(component, control, inputs, persist);
    }

    /**
     * Returns value if persistable.
     * @return any
     */
    getPersistedValue() {
        if (!this.persist) {
            return undefined;
        }
        return this.control.value === undefined ? null : this.control.value;
    }
}
